:root { }

.Promo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  min-height: 640px;
  background-color: #f4f7f9;
  text-align: center;
  overflow: hidden;
  padding: 40px 0; }

@media (max-width: 540px) {
    .Promo {
      min-height: 0; } }

.Promo__content {
    position: relative;
    z-index: 0; }

.Promo__content::before {
      content: '';
      display: block;
      position: absolute;
      right: 85%;
      top: 50%;
      width: 370px;
      height: 461px;
      background: url("/static/images/template_illustratio.svg") no-repeat center/contain;
      -webkit-transform: translate(0, -50%);
              transform: translate(0, -50%);
      z-index: -1; }

.Promo__content::after {
      content: '';
      display: block;
      position: absolute;
      left: 85%;
      top: 50%;
      width: 344px;
      height: 428px;
      background: url("/static/images/template_illustratio-2.svg") no-repeat center/contain;
      -webkit-transform: translate(0, -50%);
              transform: translate(0, -50%);
      z-index: -1; }

.Promo__img {
    max-width: 200px;
    margin: 0 0 30px; }

.Promo__title {
    font-size: 42px;
    line-height: 1.2;
    margin-bottom: 1.19048em; }

@media (max-width: 1200px) {
      .Promo__title {
        font-size: 34px; } }

@media (max-width: 540px) {
      .Promo__title {
        font-size: 26px;
        margin-bottom: 1em; } }

.Promo__text {
    font-size: 18px;
    line-height: 1.6;
    color: rgba(0, 0, 0, .45);
    margin: 0 0 70px; }

@media (max-width: 540px) {
      .Promo__text {
        font-size: 15px;
        margin-bottom: 35px; } }
