:root { }

.Recommendation {
  padding-top: 60px;
  padding-bottom: 75px;
  background-color: #f4f7f9; }

@media (max-width: 540px) {
    .Recommendation {
      padding-top: 30px;
      padding-bottom: 30px; } }

.Recommendation__content {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
    font-size: 20px;
    line-height: 1.55;
    font-weight: 300; }

@media (max-width: 1024px) {
      .Recommendation__content {
        font-size: 16px; } }

@media (max-width: 540px) {
      .Recommendation__content {
        font-size: 14px; } }

.Recommendation__text {
    white-space: pre-line; }

.Recommendation__description {
    width: 100%;
    max-width: 750px;
    margin: 0 40px 0 0; }

@media (max-width: 1024px) {
      .Recommendation__description {
        max-width: calc(100% - 200px); } }

@media (max-width: 540px) {
      .Recommendation__description {
        max-width: 100%;
        margin-right: 0; } }

.Recommendation__preview {
    width: 160px; }

@media (max-width: 540px) {
      .Recommendation__preview {
        width: 100%; } }

.Recommendation__previewText {
    font-size: 15px;
    line-height: 1.33333;
    font-weight: 400;
    margin: 10px 0 5px; }

@media (max-width: 1024px) {
      .Recommendation__previewText {
        font-size: 14px; } }

.Recommendation__previewAuthor {
    font-size: 11px;
    line-height: 16px; }

.Recommendation__img {
    width: 100%; }

.Recommendation__links {
    margin: 30px 0 0; }

@media (max-width: 540px) {
      .Recommendation__links {
        margin-bottom: 30px;
        margin-top: 20px; } }

.Recommendation__link {
    color: #5086ec;
    text-decoration: none;
    margin: 0 5px; }

.Recommendation__link:hover {
      color: #0aa13d; }
