:root { }

.Button {
  position: relative;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  min-width: 110px;
  height: 45px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.55;
  padding-left: 30px;
  padding-right: 30px;
  color: #fff;
  overflow: hidden;
  background-color: #0aa13d;
  border: none;
  z-index: 0;
  -webkit-transition: .6s;
  transition: .6s;
  cursor: pointer; }

@media (max-width: 540px) {
    .Button {
      height: 40px; } }

.Button::before {
    content: '';
    display: block;
    position: absolute;
    right: 100%;
    top: 0;
    width: 100%;
    height: 100%;
    background: #5086ec;
    -webkit-transition: .4s;
    transition: .4s;
    z-index: -1; }

.Button:hover:before {
    right: 0; }

.Button:active {
    -webkit-box-shadow: 0 0 2px rgba(0, 0, 0, .45);
            box-shadow: 0 0 2px rgba(0, 0, 0, .45); }

.Button:disabled {
    opacity: .6;
    cursor: default; }

.Button_gray {
    background: #cbcbcb; }

.Button_blue {
    background: #5086ec; }

.Button_blue:before {
      background: #0aa13d; }

.Button_lg {
    font-size: 16px;
    min-width: 200px;
    height: 55px; }

@media (max-width: 540px) {
      .Button_lg {
        min-width: 150px;
        height: 45px; } }

.Button_sm {
    height: 40px; }

@media (max-width: 540px) {
      .Button_sm {
        height: 35px; } }

.Button_xs {
    height: 35px;
    font-size: 13px;
    text-transform: uppercase; }

@media (max-width: 540px) {
      .Button_xs {
        height: 30px; } }
