:root { }

:global * {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 0;
  outline-color: #0aa13d; }

:global *:before, :global *:after {
    -webkit-box-sizing: border-box;
            box-sizing: border-box; }

:global body {
  margin: 0;
  font-family: 'Roboto', Arial, sans-serif;
  min-width: 320px;
  font-size: 16px;
  overflow-x: hidden; }

@media (max-width: 540px) {
